  --Natural-colorDarkGrey300: #333333;
  --Natural-colorDarkGrey200: #4D4D4D;
  --Natural-colorDarkGrey100: #666666;
  /* Grey */
  --Natural-colorGrey: #888888;
  --colorLightGray: #F1F1E8;
  /* Light Grey Shades */
  --Natural-colorLightGrey100: #AAAAAA;
  --Natural-colorLightGrey200: #BBBBBB;
  --Natural-colorLightGrey300: #CCCCCC;
  --Natural-colorLightGrey400: #DDDDDD;
  --Natural-colorLightGrey500: #EEEEEE;
  /* Dark Primary Shades */
  --Natural-colorDarkPrimary400: #003478;
  --Natural-colorDarkPrimary300: #0045A5;
  --Natural-colorDarkPrimary200: #0055D2;
  --Natural-colorDarkPrimary100: #0066FF;
  /* Primary */
  --Natural-colorPrimary: #2BA28B;
  --colorPrimary: #235145;
  /* Light Primary Shades */
  --Natural-colorLightPrimary100: #4AC6A2;
  --Natural-colorLightPrimary200: #6DE3BB;
  --Natural-colorLightPrimary300: #8CFFD5;
  --Natural-colorLightPrimary400: #AAFFE6;
  /* Dark Secondary Shades */
  --Natural-colorDarkSecondary400: #552200;
  --Natural-colorDarkSecondary300: #773300;
  --Natural-colorDarkSecondary200: #994400;
  --Natural-colorDarkSecondary100: #BB5500;
  /* Secondary */
  --Natural-colorSecondary: #DD6600;
  /* Light Secondary Shades */
  --Natural-colorLightSecondary100: #EE7722;
  --Natural-colorLightSecondary200: #FF8844;
  --Natural-colorLightSecondary300: #FFA766;
  --Natural-colorLightSecondary400: #FFBB88;
  /* Dark Tertiary Shades */
  --Natural-colorDarkTertiary400: #220055;
  --Natural-colorDarkTertiary300: #330077;
  --Natural-colorDarkTertiary200: #440099;
  --Natural-colorDarkTertiary100: #5500BB;
